// // import * from './Controllers/App';
// // import * from './Models/AppModel';
import './Views/AppView';
// eslint-disable-next-line no-unused-vars
import css from './Views/app-view.css';

const data = {};
let trueValue = 16;
const frames = document.querySelector('.frames');
const mainCanvas = document.querySelector('#canvas-main');
const ctx = mainCanvas.getContext('2d');

const drawingCanvas = document.querySelector('#canvas-drawing');
const drawctx = drawingCanvas.getContext('2d');

const positionCanvas = document.querySelector('#canvas-position');
const posctx = drawingCanvas.getContext('2d');


let dragSrcEl = null;

function componentToHex(c) {
  const hex = c.toString(16);
  return hex.length === 1 ? `0${hex}` : hex;
}
function rgbToHex(R, G, B) {
  return `#${componentToHex(R)}${componentToHex(G)}${componentToHex(B)}`;
}

function getActiveFrame() {
  return document.querySelector('.active-frame');
}
let startAnimation;

function startPreviewAnimation() {
  clearInterval(startAnimation);
  const textArea = document.querySelector('#fps-text');
  const { value } = document.querySelector('#fps__preview');
  textArea.innerHTML = `${value} fps`;
  const privewFrames = document.querySelectorAll('.frame');
  const backgrounds = Array.from(privewFrames).map(item => item.children[0].style.backgroundImage);
  const { length } = backgrounds;
  const previewBlock = document.querySelector('.preview__img');
  let count = 0;
  startAnimation = setInterval(() => {
    previewBlock.style.backgroundImage = backgrounds[count % length];
    count += 1;
  }, 1000 / value);
  if (value < 1) {
    clearInterval(startAnimation);
    const img = getActiveFrame().children[0].style.backgroundImage;
    previewBlock.style.backgroundImage = img;
  }
}

function getPixels() {
  data.prevPixel = trueValue;
  const minPixel = 16;
  const { value } = document.querySelector('#size__preview');
  trueValue = minPixel * (2 ** (value - 1));
  document.querySelector('#size-text').innerHTML = `${trueValue}x${trueValue} size`;
  data.truePixel = trueValue;
  return trueValue;
}

function getCanvasSize() {
  const canvasWraper = document.querySelector('.canvas');
  mainCanvas.width = canvasWraper.clientWidth;
  mainCanvas.height = canvasWraper.clientHeight;
  drawingCanvas.width = canvasWraper.clientWidth;
  drawingCanvas.height = canvasWraper.clientHeight;
  positionCanvas.width = canvasWraper.clientWidth;
  positionCanvas.height = canvasWraper.clientHeight;
}

function changeCanvas() {
  ctx.clearRect(0, 0, mainCanvas.width, mainCanvas.height);
  const image = new Image();
  const img = `${getActiveFrame().children[0].style.backgroundImage.slice(5, -2)}`;
  image.src = img;
  ctx.drawImage(image, 0, 0);
}

function changeCanvasSize() {
  getPixels();
  const frame = document.querySelector('.active-frame');
  const scale = data.truePixel / data.prevPixel;
  const image = new Image();
  const img = mainCanvas.toDataURL();
  ctx.clearRect(0, 0, mainCanvas.width, mainCanvas.height);
  image.src = img;
  ctx.drawImage(image, 0, 0, mainCanvas.width / scale, mainCanvas.height / scale);
  const newImg = mainCanvas.toDataURL();
  frame.children[0].setAttribute('style', `background: url('${newImg}');  background-size: cover`);
}

function getColors() {
  const colors = [];
  colors.push(document.querySelector('#main-color').value);
  colors.push(document.querySelector('#second-color').value);
  return colors;
}

function changeActiveFrame(item) {
  while (document.querySelector('.active-frame')) {
    document.querySelector('.active-frame').classList.remove('active-frame');
  }
  item.classList.add('active-frame');
  changeCanvas();
  startPreviewAnimation();
}

function checkQueue() {
  const allFrames = frames.children;
  for (let i = 0; i + 1 < allFrames.length; i += 1) {
    const item = allFrames[i];
    item.id = `frame${i + 1}`;
    item.querySelector('.num-frame__text').innerHTML = i + 1;
  }
}
function dragStart(e) {
  this.style.opacity = '0.4';
  dragSrcEl = this;
  e.dataTransfer.effectAllowed = 'move';
  e.dataTransfer.setData('text/html', this.innerHTML);
}

function dragEnter() {
  this.classList.add('over');
}

function dragOver(e) {
  if (e.preventDefault) {
    e.preventDefault();
  }
  e.dataTransfer.dropEffect = 'move';
  return false;
}

function dragLeave() {
  this.classList.remove('over');
}

function drop(e) {
  if (e.stopPropagation) e.stopPropagation();
  if (dragSrcEl !== this) {
    dragSrcEl.innerHTML = this.innerHTML;
    this.innerHTML = e.dataTransfer.getData('text/html');
  }
  checkQueue();
  return false;
}

function dragEnd() {
  const allFrames = frames.children;
  [].forEach.call(allFrames, (item) => {
    item.classList.remove('over');
    dragSrcEl.style.opacity = '1';
  });
  changeCanvas();
  startPreviewAnimation();
}

function removeDragbleEvents(item) {
  item.removeEventListener('dragstart', dragStart);
  item.removeEventListener('dragenter', dragEnter);
  item.removeEventListener('dragover', dragOver);
  item.removeEventListener('dragleave', dragLeave);
  item.removeEventListener('drop', drop);
  item.removeEventListener('dragend', dragEnd);
}

function addDrabbleEvents(item) {
  item.addEventListener('dragstart', dragStart);
  item.addEventListener('dragenter', dragEnter);
  item.addEventListener('dragover', dragOver);
  item.addEventListener('dragleave', dragLeave);
  item.addEventListener('drop', drop);
  item.addEventListener('dragend', dragEnd);
}

function delFrame(rowItem) {
  const item = rowItem.parentNode;
  if (item.classList.contains('active-frame')) {
    let delIndex = item.id.replace(/\D+/g, '');
    if (delIndex < 2) delIndex = 2;
    else delIndex -= 1;
    document.querySelector(`#frame${delIndex}`).classList.add('active-frame');
  }
  item.parentElement.removeChild(item);
  checkQueue();
  changeCanvas();
  removeDragbleEvents(item);
  startPreviewAnimation();
}

function copyFrame(rowItem) {
  const item = rowItem.parentNode;
  const cloned = item.cloneNode(true);
  item.parentNode.insertBefore(cloned, item.nextSibling);
  checkQueue();
  changeActiveFrame(cloned);
  addDrabbleEvents(cloned);
  startPreviewAnimation();
}

function frameSelected() {
  let clickTarget = window.event.target;
  let action;
  while (clickTarget !== this) {
    if (clickTarget.tagName === 'DIV') {
      action = clickTarget.dataset.frameAction;
      break;
    }
    clickTarget = clickTarget.parentElement;
  }
  if (action === 'changeActiveFrame') changeActiveFrame(clickTarget.parentElement);
  else if (action === 'none');
  else if (action === 'delFrame') delFrame(clickTarget.parentElement);
  else if (action === 'copyFrame') copyFrame(clickTarget.parentElement);
  else if (!clickTarget.hasAttribute('data-frame-action')) changeActiveFrame(clickTarget);
}

function addFrame() {
  const count = frames.childElementCount;
  const newDiv = document.createElement('div');
  newDiv.setAttribute('draggable', 'true');
  newDiv.className = 'frame';
  newDiv.id = `frame${frames.childElementCount}`;
  newDiv.innerHTML = `
  <div class="frame-wrapper" data-frame-action='changeActiveFrame'>
    <div class="num-frame" data-frame-action='changeActiveFrame'>
      <p class="num-frame__text">${frames.childElementCount}</p>
    </div>
    <div class="del-frame" data-frame-action='delFrame'>
      <p class="del-frame__text">del</p>
    </div>
    <div class="copy-frame" data-frame-action='copyFrame'>
      <p class="copy-frame__text">copy</p>
    </div>
  </div>`;
  frames.insertBefore(newDiv, frames.children[count - 1]);
  newDiv.style.backgroundImage = 'url(data:image/png;base64,';
  changeActiveFrame(newDiv);
  addDrabbleEvents(newDiv);
  startPreviewAnimation();
}

function changePenSize() {
  const block = document.querySelector('#pen-size');
  const { value } = block;
  const text = document.querySelector('#pen-text');
  text.innerHTML = `${value} size`;
  return value;
}

function saveCanvas(base64) {
  const frame = document.querySelector('.active-frame');
  let img = base64;
  const image = new Image();
  image.src = img;
  image.onload = () => {
    ctx.drawImage(image, 0, 0);
    drawctx.clearRect(0, 0, mainCanvas.width, mainCanvas.height);
    img = mainCanvas.toDataURL();
    frame.children[0].setAttribute('style', `background: url('${img}');  background-size: cover`);
    startPreviewAnimation();
  };
}

function activeInstrument() {
  const instrument = document.querySelector('.active-instrument');
  const action = instrument.dataset.instrument;
  return action;
}

function changeActiveInstrument(item) {
  while (document.querySelector('.active-instrument')) {
    document.querySelector('.active-instrument').classList.remove('active-instrument');
  }
  item.classList.add('active-instrument');
}

function getActiveInstrument() {
  let clickTarget = window.event.target;
  let action;
  while (clickTarget !== this) {
    if (clickTarget.tagName === 'DIV') {
      action = clickTarget.dataset.instrument;
      break;
    }
    clickTarget = clickTarget.parentElement;
  }
  changeActiveInstrument(clickTarget);
  return action;
}

function pixelModificator() {
  if (activeInstrument() === 'dropper') {
    const pixel = ctx.getImageData(window.event.layerX, window.event.layerY, 1, 1).data;
    const value = rgbToHex(pixel[0], pixel[1], pixel[2]);
    if (window.event.buttons === 1) document.querySelector('#main-color').value = value;
    else if (window.event.buttons === 2) document.querySelector('#second-color').value = value;
  } else if (activeInstrument() === 'brighter') {
    const X = window.event.layerX;
    const Y = window.event.layerY;
    const step = data.W / getPixels();
    const penSize = changePenSize();
    const positionX = Math.floor(X / step);
    const positionY = Math.floor(Y / step);
    const imgData = ctx.getImageData(positionX * step, positionY * step, step * penSize, step * penSize);

    if (window.event.buttons === 1) {
      for (let i = 0; i < imgData.data.length; i += 4) {
        imgData.data[i] += 10;
        imgData.data[i + 1] += 10;
        imgData.data[i + 2] += 10;
        imgData.data[i + 3] = imgData.data[i + 3];
      }
    } else if (window.event.buttons === 2) {
      for (let i = 0; i < imgData.data.length; i += 4) {
        imgData.data[i] -= 10;
        imgData.data[i + 1] -= 10;
        imgData.data[i + 2] -= 10;
        imgData.data[i + 3] = imgData.data[i + 3];
      }
    }
    ctx.putImageData(imgData, positionX * step, positionY * step);
  }
}
function openFullscreen() {
  const preview = document.querySelector('#preview__img');
  if (preview.requestFullscreen) {
    preview.requestFullscreen();
  } else if (preview.mozRequestFullScreen) {
    /* Firefox */
    preview.mozRequestFullScreen();
  } else if (preview.webkitRequestFullscreen) {
    /* Chrome, Safari and Opera */
    preview.webkitRequestFullscreen();
  }
}
function setCoordinates() {
  const penSize = changePenSize();
  posctx.clearRect(0, 0, data.W, data.H);
  posctx.fillStyle = 'rgba(10, 10, 10, 0.3)';
  const step = data.W / getPixels();
  const X = Math.floor(window.event.layerX / step);
  const Y = Math.floor(window.event.layerY / step);
  document.querySelector('#coordnates-text').innerHTML = `X:${X + 1} Y:${Y + 1}`;

  posctx.fillRect(X * step, Y * step, step * penSize, step * penSize);

  drawingCanvas.addEventListener('mousedown', () => {
    document.querySelector('#coordnates-text').innerHTML = `X:${X + 1} Y:${Y + 1}`;
    drawingCanvas.removeEventListener('mousemove', setCoordinates);
  });
  drawingCanvas.addEventListener('mouseup', () => {
    drawingCanvas.addEventListener('mousemove', setCoordinates);
  });
}

function drawSquare(startX, startY, endX, endY, step, penSize) {
  drawctx.lineWidth = penSize * step;
  drawctx.strokeRect(startX * step + step / 2, startY * step + step / 2, (endX - startX) * step, (endY - startY) * step);
}

function startMoveCanvas() {
  let X = window.event.layerX;
  let Y = window.event.layerY;
  const step = data.W / getPixels();
  const startX = Math.floor(X / step);
  const startY = Math.floor(Y / step);

  let top = 0;
  let left = 0;

  let img = mainCanvas.toDataURL();
  const image = new Image();
  image.src = img;

  function moveCanvas() {
    X = window.event.layerX;
    Y = window.event.layerY;
    const endX = Math.floor(X / step);
    const endY = Math.floor(Y / step);
    left = (endX - startX) * step;
    top = (endY - startY) * step;
    mainCanvas.style.top = `${top}px`;
    mainCanvas.style.left = `${left}px`;
  }
  function upCanvas() {
    img = mainCanvas.toDataURL();
    const frame = document.querySelector('.active-frame');
    drawingCanvas.removeEventListener('mousemove', moveCanvas);
    ctx.clearRect(0, 0, mainCanvas.width, mainCanvas.height);
    ctx.drawImage(image, left, top);
    img = mainCanvas.toDataURL();
    frame.children[0].setAttribute('style', `background: url('${img}');  background-size: cover`);
    mainCanvas.style.top = 0;
    mainCanvas.style.left = 0;
    drawingCanvas.removeEventListener('mouseup', upCanvas);
    startPreviewAnimation();
  }
  drawingCanvas.addEventListener('mousemove', moveCanvas);
  drawingCanvas.addEventListener('mouseup', upCanvas);
}

function ditheringDraw(oldX, oldY, step) {
  const penSize = changePenSize();
  let colors = getColors();
  if (window.event.buttons === 2) colors = colors.reverse();
  for (let i = 0; i < penSize; i += 1) {
    for (let j = 0; j < penSize; j += 1) {
      if (((oldX + j) % 2 === 0 && (oldY + i) % 2 === 0)
        || ((oldX + j) % 2 === 1 && (oldY + i) % 2 === 1)) {
        drawctx.fillStyle = colors[0];
      } else {
        drawctx.fillStyle = colors[1];
      }
      drawctx.fillRect((oldX + j) * step, (oldY + i) * step, step, step);
    }
  }
}


function startDraw() {
  if (activeInstrument() === 'pen'
    || activeInstrument() === 'eraser'
    || activeInstrument() === 'mirror'
    || activeInstrument() === 'square'
    || activeInstrument() === 'line'
    || activeInstrument() === 'dithering');
  else if (activeInstrument() === 'hand') {
    startMoveCanvas();
    return;
  } else return;
  ctx.globalCompositeOperation = 'source-over';
  const colors = getColors();
  if (window.event.buttons === 1) {
    drawctx.fillStyle = colors[0]; drawctx.strokeStyle = colors[0];
  } else if (window.event.buttons === 2) {
    drawctx.fillStyle = colors[1]; drawctx.strokeStyle = colors[1];
  }
  if (activeInstrument() === 'eraser') ctx.globalCompositeOperation = 'destination-out';
  let X = window.event.layerX;
  let Y = window.event.layerY;
  const step = data.W / getPixels();
  const penSize = changePenSize();
  let positionX = Math.floor(X / step);
  let positionY = Math.floor(Y / step);
  let oldX = positionX;
  let oldY = positionY;
  const startX = oldX;
  const startY = oldY;
  if (activeInstrument() === 'dithering') {
    ditheringDraw(oldX, oldY, step);
  }
  if (activeInstrument() === 'eraser') {
    ctx.fillRect(oldX * step, oldY * step, step * penSize, step * penSize);
    posctx.clearRect(0, 0, data.W, data.H);
  } else if (activeInstrument() !== 'dithering') drawctx.fillRect(oldX * step, oldY * step, step * penSize, step * penSize);
  if (activeInstrument() === 'mirror') drawctx.fillRect(data.W - (oldX * step) - (penSize * step), oldY * step, step * penSize, step * penSize);

  function canvasMove() {
    if (activeInstrument() === 'line'
      || activeInstrument() === 'square') {
      drawctx.clearRect(0, 0, data.W, data.H);
      oldX = startX;
      oldY = startY;
    }
    X = window.event.layerX;
    Y = window.event.layerY;
    positionX = Math.floor(X / step);
    positionY = Math.floor(Y / step);
    document.querySelector('#coordnates-text').innerHTML = `X:${positionX + 1} Y:${positionY + 1}`;
    if (activeInstrument() === 'square') {
      drawSquare(oldX, oldY, positionX, positionY, step, penSize);
      return;
    }
    const dx = Math.abs(positionX - oldX);
    const dy = Math.abs(positionY - oldY);
    const sx = (oldX < positionX) ? 1 : -1;
    const sy = (oldY < positionY) ? 1 : -1;
    let err = dx - dy;
    while (window) {
      if (activeInstrument() === 'dithering') {
        ditheringDraw(oldX, oldY, step);
      }
      if (activeInstrument() === 'eraser') {
        ctx.fillRect(oldX * step, oldY * step, step * penSize, step * penSize);
        posctx.clearRect(0, 0, data.W, data.H);
      } else if (activeInstrument() !== 'dithering') drawctx.fillRect(oldX * step, oldY * step, step * penSize, step * penSize);
      if (activeInstrument() === 'mirror') drawctx.fillRect(data.W - (oldX * step) - (penSize * step), oldY * step, step * penSize, step * penSize);
      if ((oldX === positionX) && (oldY === positionY)) break;
      const e2 = 2 * err;
      if (e2 > -dy) { err -= dy; oldX += sx; }
      if (e2 < dx) { err += dx; oldY += sy; }
    }
  }

  function onMouseUp() {
    drawingCanvas.removeEventListener('mousemove', canvasMove);
    const img = drawingCanvas.toDataURL();
    saveCanvas(img);
    ctx.globalCompositeOperation = 'source-over';
    drawingCanvas.removeEventListener('mouseup', onMouseUp);
    drawingCanvas.removeEventListener('mouseout', onMouseUp);
  }
  drawingCanvas.addEventListener('mouseup', onMouseUp);
  drawingCanvas.addEventListener('mouseout', onMouseUp);
  drawingCanvas.addEventListener('mousemove', canvasMove);
}

function onLoad() {
  getCanvasSize();
  getPixels();
  addDrabbleEvents(document.querySelector('.frame'));
  data.W = mainCanvas.width;
  data.W = drawingCanvas.width;
  data.H = mainCanvas.height;
  data.H = drawingCanvas.width;
  startPreviewAnimation();
}
onLoad();
document.querySelector('.main__instriments').addEventListener('click', getActiveInstrument);
drawingCanvas.addEventListener('contextmenu', (e) => { e.preventDefault(); });
drawingCanvas.addEventListener('mousedown', startDraw);
drawingCanvas.addEventListener('mousedown', pixelModificator);
document.querySelector('.frames').addEventListener('click', frameSelected);
document.querySelector('#add-frame-button').addEventListener('click', addFrame);
document.querySelector('#pen-size').addEventListener('change', changePenSize);
document.querySelector('#size__preview').addEventListener('change', changeCanvasSize);
document.querySelector('#fps__preview').addEventListener('change', startPreviewAnimation);
drawingCanvas.addEventListener('mousemove', setCoordinates);
document.querySelector('#preview__img').addEventListener('dblclick', openFullscreen);
